










































import { Component, Vue } from 'vue-property-decorator';
import AddressInput from '@/components/AddressInput.vue';
import AmountInput from '@/components/AmountInput.vue';
import {
  emptyDescription,
  StepDescription,
  Token,
  TokenPlaceholder
} from '@/model/types';
import { BalanceUtils } from '@/utils/balance-utils';
import Stepper from '@/components/Stepper.vue';
import ErrorScreen from '@/components/ErrorScreen.vue';
import Divider from '@/components/Divider.vue';
import TokenInformation from '@/components/TokenInformation.vue';
import ActionButton from '@/components/ActionButton.vue';

@Component({
  components: {
    ActionButton,
    TokenInformation,
    Divider,
    AddressInput,
    AmountInput,
    Stepper,
    ErrorScreen
  }
})
export default class Payment extends Vue {
  target: string = '';
  token: Token = TokenPlaceholder;
  amount: string = '0';

  valid: boolean = false;
  loading: boolean = false;
  done: boolean = false;

  error: string = '';

  steps: StepDescription[] = [];
  doneStep: StepDescription = emptyDescription();

  async created() {
    const { token } = this.$route.params;
    this.token = (await this.$raiden.getToken(token)) || TokenPlaceholder;
    this.steps = [
      (this.$t('payment.steps.transfer') as any) as StepDescription
    ];
    this.doneStep = (this.$t('payment.steps.done') as any) as StepDescription;
  }

  async transfer() {
    const { address, decimals } = this.token;
    try {
      this.loading = true;
      await this.$raiden.transfer(
        address,
        this.target,
        BalanceUtils.parse(this.amount, decimals)
      );
      this.done = true;
      setTimeout(() => {
        this.loading = false;
        this.done = false;
      }, 2000);
    } catch (e) {
      this.loading = false;
      this.done = false;
      this.error = e.message;
    }
  }
}
